
import React, { useContext, useEffect, useState } from 'react';
import { Button , Nav } from 'react-bootstrap';
import { useHistory , useParams } from 'react-router-dom';
import styled from 'styled-components';
import { CSSTransition } from 'react-transition-group';


import './ProductDetail.scss';

import { prodStoredContext } from './Home';
 
let StyledDiv = styled.div `padding: 10px; `;
let StyledH4 = styled.h4 `
        font-size: 20px; 
        color: ${props => props.color}
        `;






function Detail (props) {
  
  let history = useHistory();

  let { productNumber } = useParams();

  let product = props.productData.find(x => x.id === Number(productNumber));
 
  let [alertStat,changeAlertStat] = useState(true);


  let [tabState,changeTabState] = useState(0);
  let [tabAnim, changeTabAim] = useState(false);

  let tabChangfunction = (tab) => {
    changeTabState(tab);
  }

  //마운트 
  useEffect( () => {
   let setAlertTimer = setTimeout(()=>{
      changeAlertStat(false);
       //언마운트  
       return() => { clearTimeout(setAlertTimer) }
    } , 2000);
  } , [alertStat] );



  return(

    <div className="container">
        <StyledDiv>
          <StyledH4 className="red" >
            상품안내
            <hr/>
          </StyledH4>
        </StyledDiv>
      <div className="product-alert" id="product-alert"  style={{display: `${alertStat?  "" : "none"}` }}>
        <p>재고가 얼마남지 않았습니다.</p>
      </div>
      <div className="row">
        <div className="col-md6" >
          <img src={props.productData[product.id].imgsrc} alt=""  width="100%"/>
        </div>
        <div className="col-md6 mt-4 mx-auto" >
          <h4 className="pt-5">
            {props.productData[product.id].title}
          </h4>
          <p>{props.productData[product.id].content}</p>
          <p>{props.productData[product.id].price}</p>

          <StoredInfo  productId={product.id} />

          <hr/>
          <button className="btn btn-danger" onClick={ () => { props.changProdStored([8,8,8]) } }  >주문하기</button>{' '}
          <Button variant="primary" onClick={ () => {history.goBack()} } > 뒤로가기 </Button>
        </div>
      </div>


      {/* tab  */}
      <Nav className="mt-5"  variant="tabs"  defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>{ changeTabAim(false); changeTabState(0)}}  >Active</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>{ changeTabAim(false); changeTabState(1)}}>Option 2</Nav.Link>
        </Nav.Item>
      </Nav>

      <CSSTransition in={tabAnim} classNames="wow" timeout={500}>
        <TabContent tabState={tabState} changeTabAim={changeTabAim}/>  
      </CSSTransition>
        


      
    </div>
  )

}

function TabContent(props){

  useEffect( () => {
    props.changeTabAim(true);
  })

  if(props.tabState === 0){
    return (
      <div className="tabDiv">aaa</div>
    )
  }else if(props.tabState === 1){
    return (
      <div className="tabDiv" >bbb</div>
    )
  }else if(props.tabState === 2){
    return (
      <div className="tabDiv" >ccc</div>
    )
  }
}


function StoredInfo(props) {

  let prodStored = useContext(prodStoredContext);

  return (
    <p>재고: {prodStored[props.productId]}</p>
  ) 

}


export default Detail;